/* Page Library Component Styles */

.page-library-container {
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f5f5f5;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  overflow: hidden;
}

/* Level 1 Header */
.level-1-header {
  background-color: #ffffff;
  padding: 12px 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  font-weight: 600;
  color: #1a1a1a;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border-bottom: 1px solid #e0e0e0;
  transition: background-color 0.2s ease;
}

.level-1-header:hover {
  background-color: #f8f8f8;
}

.level-1-header .chevron {
  font-size: 10px;
  color: #666;
  transition: transform 0.2s ease;
  display: inline-block;
  width: 12px;
}

.level-1-header .heading-text {
  flex: 1;
}

/* Level 2 Container */
.level-2-container {
  background-color: #ffffff;
}

.level-2-item {
  border-bottom: 1px solid #f0f0f0;
}

.level-2-item:last-child {
  border-bottom: none;
}

/* Level 2 Header */
.level-2-header {
  padding: 10px 16px 10px 32px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  font-weight: 500;
  color: #2a2a2a;
  transition: background-color 0.2s ease;
}

.level-2-header:hover {
  background-color: #f8f8f8;
}

.level-2-header .chevron {
  font-size: 9px;
  color: #666;
  transition: transform 0.2s ease;
  display: inline-block;
  width: 10px;
}

.level-2-header .heading-text {
  flex: 1;
}

/* Level 3 Container (Links) */
.level-3-container {
  background-color: #fafafa;
  padding: 8px 0;
}

.link-list {
  display: flex;
  flex-direction: column;
}

.link-item {
  padding: 8px 16px 8px 56px;
  color: #0066cc;
  text-decoration: none;
  font-size: 13px;
  transition: all 0.2s ease;
  display: block;
  border-left: 3px solid transparent;
}

.link-item:hover {
  background-color: #f0f0f0;
  border-left-color: #0066cc;
  padding-left: 60px;
}

.link-item:active {
  color: #004499;
}

/* Loading and Empty States */
.loading,
.no-links {
  padding: 12px 16px 12px 56px;
  color: #666;
  font-size: 13px;
  font-style: italic;
}

/* Responsive Design */
@media (max-width: 768px) {
  .level-1-header {
    padding: 10px 12px;
    font-size: 13px;
  }

  .level-2-header {
    padding: 8px 12px 8px 24px;
    font-size: 13px;
  }

  .link-item {
    padding: 8px 12px 8px 40px;
    font-size: 12px;
  }

  .link-item:hover {
    padding-left: 44px;
  }

  .loading,
  .no-links {
    padding: 10px 12px 10px 40px;
    font-size: 12px;
  }
}

@media (max-width: 480px) {
  .level-1-header {
    padding: 8px 10px;
    font-size: 12px;
  }

  .level-2-header {
    padding: 6px 10px 6px 20px;
    font-size: 12px;
  }

  .link-item {
    padding: 6px 10px 6px 32px;
    font-size: 11px;
  }

  .link-item:hover {
    padding-left: 36px;
  }

  .loading,
  .no-links {
    padding: 8px 10px 8px 32px;
    font-size: 11px;
  }
}
